export default {

	data() {
		return {
			error: undefined,
			data: undefined,
			language: this.$nuxt.$i18n.locale
		}
	},

	created: async function(){

		await this.$axios.post('/category/home').then((data) => {

			if (!data['data']['status']) {
				this.error = data['data']['data']
			}

			this.data = data['data']['data']

		})

	}
}
